const cp = `
	Competitive Programming is something I started a little before my sophomore year. There are a lot of problems that I think are super interesting that I really solving, and a lot of others to be honest just felt like completing chores. Nevertheless, I've solved over 500+ problems on <https://codeforces.com/profile/marvalarva2929> and am ranked Gold in USACO. I'm not sure if comp programming is something I still want to continue doing but it oculd be. I really like combinatorics problems so maybe I'll just keep doing those :)
	
	You can see my comp prog github @ <https://github.com/marvalarva2929/Competitive_Programming>
`
const ge = `
	This was a really fun project to work on. Basically the program just loads a .obj file and renders a 3d model of it. You can move around with wasd and look around with ijkl. There were multiple steps to this project.

	 1: Load a window with SDL2
	 2: Figure out how to render singular points
	 3: Figure out how to load a .obj
	 4: Load all the points in the .obj and connect them!
	
	This is really all I did.
	Here's step 1:
	\`\`\`SDL_CreateWindowAndRenderer(WINDOW_WIDTH, WINDOW_HEIGHT, SDL_WINDOW_SHOWN, &window, &renderer);\`\`\`

	Step 2 was a little more complicated. There are a series of matrix multiplications that have to go into projecting a 3d point onto a 2d surface. But basically you take the (x, y, z) coordinates of the point relative to the camera, put them in a special matrix, and out comes the coordinates of the point on the screen. Here's what that looks like:
	
	for (auto &point : object.Points) {
            point = rotate * (point + camera); 
            pnts.emplace_back(fin * point); 
    }

	Where \`\`rotate\`\` is the rotation matrix, \`\`camera\`\` is the relative location of the camera, and \`\`Matrix fin = viewport * projection_matrix; \`\`
	All this can be found in the math.cpp file in the repo.

	Step 3 was just figuring out how a .obj file lists all the vertices and edges. Once I figured that out I process it like this:
	
	\`\`\` while (!file.eof()) {
        std::string c;
        std::getline(file, c);
        if (c[0] == 'v') {
            std::string vert = c.substr(1);
            std::stringstream ss; ss << vert;  
            float one, two, three;
            ss >> one >> two >> three;
            Points.emplace_back(one * 0.02f, two * 0.02f, three * 0.02f); 
        } else if (c[0] == 'f') {
            std::string face = c.substr(1);
            std::stringstream ss; ss << face;
            int last = -1; 
            while (!ss.eof()) {
                int ind; ss >> ind; ind--;
                if (last != -1)
                    Edges.emplace_back(last, ind);
                last = ind;
            }
        }
    } \`\`\`

	Step 4 just connects each point specified by the edges in the file. Looks like this:

	\`\`\` for (Object& object : objects) {
        for (auto edge : object.Edges) {
            auto p1 = pnts[edge.first], p2 = pnts[edge.second];
            if (std::min({p1.x, p1.y, p2.x, p2.y}) >= 0 && std::max({p1.z, p2.z}) < 1)
                SDL_RenderDrawLine(renderer, p1.x, p1.y, p2.x, p2.y);
        }
    } \`\`\`

	Besides SDL Boilerplate, this is pretty much everything I needed to code to get my code to work. If you wanna try it out for yourself, just download the repo https://github.com/marvalarva2929/epicgameengine and do ./debug.sh in the root directory!
`
